@import "./../../themes/colors.scss";
.contact {
  background-color: $pageBackground !important;
  box-shadow: none !important;
  color: rgb(167, 167, 167) !important;
}
.primaryText {
  color: $primary !important;
}

.iconStyle {
  font-size: 3rem !important;
  color: $primary;
}

.socialMediaIcons {
  svg {
    font-size: 3rem !important;
  }
  padding-left: 0.3rem;
  padding-right: 0.3rem;
}

.divider {
  height: 1px;
  width: 25rem;
  border-bottom: 2px solid;
  padding-top: 1.5rem;
}

.inputForm {
  div {
    padding-left: 1rem;
    padding-right: 1rem;
    padding-bottom: 0.5rem;
    padding-top: 0.5rem;
    width: 100%;
    input {
      width: 100%;
      background: transparent;
      height: 2.5rem;
      padding: 0.5rem;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.85);
      border: 0px;
      color: grey;
      font-size: 16px;
    }
    textarea {
      width: 100%;
      background: transparent;
      padding: 0.5rem;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.85);
      border: 0px;
      color: grey;
      font-size: 16px;
    }
  }
}
