@import "./../../themes/colors.scss";

.projectCard {
  text-align: left;
  padding: 1rem;
  width: 48%;
  height: 32rem;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.85);
  h5 {
    color: $text;
    text-shadow: 0px 0px 20px $primary;
    font-weight: bold;
  }
  .chip {
    color: $primary;
    font-weight: bold;
    font-size: 1rem;
    svg {
      color: $primary;
    }
  }
  .textArea {
    padding-top: 0.6rem !important;
    div {
      padding: 1rem;
    }
    textarea {
      color: $text;
    }
    label {
      div {
        color: $text;
        font-size: 18px;
      }
    }
  }
  .itemKey {
    color: $textDark;
    font-weight: bold;
  }
  .itemLabel {
    color: $text;
  }
  margin-bottom: 3rem;
}
