@import "./../../themes/colors.scss";

.skillCard {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(45%, 2fr));
  width: 100%;
  border-radius: 10px;
  padding: 1rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.85);
  .progress {
    color: $primary;
    border-radius: 5px;
    background: white;
    font-weight: bold;
    padding-left: 5px;
    padding-right: 15px;
  }
}

.category {
  border-radius: 5px;
  box-shadow: 0 0 20px 0px #585959;
  padding: 1rem;
  margin: 1rem;
  h5 {
    text-shadow: 0px 0px 5px $primary;
    font-weight: bold;
  }
}
