@import "./../../themes/colors.scss";

.root {
  background-color: $background !important;
}

.header-holder {
  position: fixed;
  right: 0;
  z-index: 50;
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  padding: 70px 0;
  background-color: #111;
}
.animate__zoomIn {
  -webkit-animation-name: zoomIn;
  animation-name: zoomIn;
}
.animate__animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-duration: var(--animate-duration);
  animation-duration: var(--animate-duration);
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.text-center {
  text-align: center !important;
}

.navigation {
  width: 70px;
  height: 100%;
  position: relative;
  border-radius: 10px 0 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #212121;
  overflow-y: scroll;
  overflow: visible;
}

.navigation ul {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
}
.nav {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}
dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 1rem;
}
ol,
ul {
  padding-left: 2rem;
}

.navigation ul li {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;
}
li.active {
  position: absolute;
  width: 70px;
  height: 70px;
  left: -50%;
  border-radius: 50%;
  border: 6px solid #111;
  transition: 0.5s;
  background-image: linear-gradient(#232323, #141516d1) !important;
  svg {
    color: $primary !important;
  }
}
li {
  display: inline-block;
}
.clearfix:after,
.clr:after,
.grve-container:after,
li:after,
ul:after {
  clear: both;
  display: block;
  content: "";
}
.clearfix:after,
.clr:after,
.container:after,
.grve-container:after,
li:after,
ul:after {
  clear: both;
  display: block;
  content: "";
}
*,
::after,
::before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box;
}
*,
::after,
::before {
  box-sizing: border-box;
}

.navigation ul li a .icon {
  position: relative;
  display: block;
  text-align: center;
  font-size: 2.5rem;
  transition: 0.5s;
  color: #5f5f5f;
  svg {
    font-size: 2.3rem !important;
  }
}

span {
  text-decoration: none;
}

.navigation ul li.active a .icon {
  color: $primary !important;
}

.social__tooltip-bottom {
  left: 0;
  transform: translateX(-100%);
}
.social__tooltip {
  opacity: 0;
  position: absolute;
  padding: 0.5rem 1rem;
  background-color: $primary;
  color: #fff;
  font-size: 1rem;
  white-space: nowrap;
  font-weight: 700;
  border-radius: 0.5rem;
  pointer-events: none;
}

.social__tooltip:hover {
  opacity: 1;
}

li.active::before {
  content: "";
  position: absolute;
  left: 50%;
  top: -37.5%;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border-bottom-left-radius: 20px;
  box-shadow: -10px 1px 0 0 #111;
}

li.active::after {
  content: "";
  position: absolute;
  bottom: -37.5%;
  right: 9px;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border-top-left-radius: 20px;
  box-shadow: -10px -1px 0 0 #111;
}

.social__tooltip-bottom::after {
  top: 50%;
  transform: translate(50%, -50%) rotate(45deg);
  right: 1px;
}
.social__tooltip::after {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  background-color: #ff651c;
  border-radius: 3px;
}
