@import "./../../themes/colors.scss";

.resumeCard {
  width: 98%;
  min-height: 12rem;
  min-height: auto;

  background-color: #232323;
  border-left: 3px solid $primary;
  padding: 32px 48px;
  position: relative;
  border-radius: 10px;
  margin-bottom: 30px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.85);
  text-align: left;

  h5 {
    font-weight: bold;
  }

  h6 {
    color: $text !important;
  }

  .item-arrow::before {
    content: "";
    display: block;
    position: absolute;
    height: 16px;
    width: 16px;
    left: 0;
    background-color: $primary;
  }
  .item-arrow::after {
    content: "";
    display: block;
    position: absolute;
    border: 8px solid transparent;
    left: 16px;
    border-left-color: $primary;
  }
}
