$primary: #00a2ff;
$primaryLightShade: lighten($primary, 50);
$primaryDarkShade: darken($primary, 5);
$background: #111;
$text: rgb(235, 234, 234);
$textLight: rgb(193, 193, 193);
$textDark: rgb(140, 140, 140);
$pageBackground: #212121;

button {
    background-color: darken($primaryDarkShade, 10) !important;
}

::-webkit-scrollbar {
    width: 15px;
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: $primary;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
    background: $primary;
}